body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  display: table;
  width: 100%;
}

.runtime-status-failed {
    background-color: rgb(255, 234, 228);
}

.runtime-status-completed {
    background-color: rgb(242, 250, 240);
}

.runtime-status-running {
    background-color: rgb(252, 250, 236);
}

.runtime-status-listening {
    background-color: rgb(236, 244, 252);
}

.runtime-status-terminated {
    background-color: rgb(235, 235, 235)
}

.top-toolbar {
    padding-top: 5px;
    padding-bottom: 15px;
}

.toolbar-select {
    min-width: 150px
}

.long-text-cell {
    min-width: 200px;
    max-width: 250px;
}

.long-text-cell-input {
    font-size: x-small !important;
}

.empty-table-placeholder {
    padding: 10px;
    text-align: center;
}

.name-cell {
    max-width:160px;
    word-break: break-all;
}
.title-typography {
    padding-right: 10px;
}

.app-bar {
    margin-bottom: 10px;
}
.error-snackbar {
    top: 80px !important;
    padding-left: 20px;
}

.error-snackbar-content {
    background-color: red !important;
}

.error-icon {
    margin-right: 10px;
}

.datetime-cell {
    min-width: 190px;
}

.till-checkbox {
    padding-top: 20px !important;
    padding-bottom: 4px !important;
}

.till-label {
    padding-left: 15px !important;
}

.till-input {
    width: 215px;
}

.items-count-label {
    float:right;
    padding-right: 20px;
    margin-top: 0px !important;
}

.instance-id-cell {
    max-width: 270px;
    word-break: break-all;
}

.grid-container {
    padding-right: 15px;
}

.grid-item {
    padding-left: 15px;
}

.details-datetime-cell {
    min-width: 250px;
}

.history-events-count-label {
    padding-top: 10px;
    padding-left: 16px;
    padding-bottom: 10px;
}
